import React, { Component } from 'react';
import './recognitionsPage.css';
import EmpOfMonth from './../images/recognitions/EmployeOfTheMonthNomination.png';
import Appreciation from './../images/recognitions/Appreciations_Atmecs_Kudos.jpg';
import NewsLetter from './../images/recognitions/Appreciations_NewsLetter.jpg';

interface Dictionary<T> {
    [Key: string]: T;
}

const RecognitionsExplanation: Dictionary<string> =  {
    EmpOfMonth: "Received nomination for employee of the month from Client",
    Appreciation: "Appreciation received from ATMECS for successful and persistent efforts across the project",
    NewsLetter: "A Module developed by me, was highlighted in newsletter for successfully meeting the deadlines way before than expected"
};

export default class RecognitionsPage extends Component<any, any>{

    constructor(props: any){
        super(props);
        this.state = {
            selectedImgIndx: 1,
            EmpOfMonth: "UnselectedImageLeft",
            Appreciation: "SelectedImage",
            NewsLetter: "UnselectedImageRight",
            DescriptionDisplayStyle:{
                width: 0, height: 0, visibility: "hidden"
            },
            selectedImagePath: "Appreciation"
        }

        this.selectedImageMouseOut = this.selectedImageMouseOut.bind(this);
        this.selectedImageMouseOver = this.selectedImageMouseOver.bind(this);
    }

    selectImage(recognitionImage: string){
        let selectedImage = document.getElementsByClassName("SelectedImage")[0];
        selectedImage.removeEventListener("mouseover", this.selectedImageMouseOver);
        selectedImage.removeEventListener("touchstart", this.selectedImageMouseOver);
        let selectedImageCss = this.state[recognitionImage];
        
        let previouslySelectedImage = 'NewsLetter';
        
        if(this.state.EmpOfMonth === "SelectedImage"){
            previouslySelectedImage = 'EmpOfMonth';
        }
        
        else if(this.state.Appreciation === "SelectedImage"){
            previouslySelectedImage = 'Appreciation';
        }
        
        var obj: Dictionary<string> = {};
        obj[recognitionImage] = "SelectedImage";
        obj[previouslySelectedImage] = selectedImageCss;
        obj["selectedImagePath"] = recognitionImage;
        this.setState(obj, () => {
            let selectedImage = document.getElementsByClassName("SelectedImage")[0];
            selectedImage.addEventListener("mouseover", this.selectedImageMouseOver);
            selectedImage.addEventListener("touchstart", this.selectedImageMouseOver);
        })
    }

    selectedImageMouseOver() {
        let selectedImage = document.getElementsByClassName("SelectedImage")[0];
        this.setState({DescriptionDisplayStyle: {width: selectedImage.scrollWidth, height: selectedImage.scrollHeight, visibility: "visible"}});
        setTimeout(() => 
        {
            this.selectedImageMouseOut();
        },
        2000);
    }

    selectedImageMouseOut() {
        this.setState({DescriptionDisplayStyle: {width: 0, height: 0, visibility: "hidden"}});
    }

    componentDidMount(){
        this.selectImage('Appreciation');
    }

    render()
    {   
        let comp = this;

        return(
            <div className="RecognitionsContainer h-100">
                <div className="row">
                    <div className="col">
                        <h1 className="text-center Title">RECOGNITIONS</h1>
                    </div>
                </div>
                
                <div className="ImagesDiv d-flex align-items-center justify-content-center">
                   
                        <img src={EmpOfMonth} 
                            className={comp.state.EmpOfMonth} 
                            alt={EmpOfMonth} 
                            onClick={() => comp.selectImage('EmpOfMonth')}/>

                        <img src={Appreciation} 
                            className={comp.state.Appreciation}
                            alt={Appreciation}
                            onClick={() => comp.selectImage('Appreciation')}/>
                        <span className="d-flex ImageDescription text-center align-items-center" style={comp.state.DescriptionDisplayStyle}>
                            <span className="p-1">{RecognitionsExplanation[comp.state.selectedImagePath]}</span>
                        </span>
                   
                        <img src={NewsLetter} 
                            className={comp.state.NewsLetter}
                            alt={NewsLetter}
                            onClick={() => comp.selectImage('NewsLetter')}/>
                   
                </div>
            </div>
        );
    }

}