import React from 'react';
import './aboutPage.css';

export default function AboutPage(){
    return(
        <div className="container-fluid AboutContainer">
            <div className="row mt-2">
                <div className="col">
                <p className="MainTitle text-center">Mission Driven Software Engineer</p>
                </div>
            </div>
            <div className="row mt-2">
                <div className="col">

                    <p className="Title text-center">GET TO KNOW ME</p>
                    
                    <p className="EducationText">
                        <span className="Qualification">PG Diploma in ML and AI</span> - IIIT Bangalore</p>
                    <p className="EducationText">
                        <span className="Qualification">B.Tech in Computer Science and Engineering</span> - VIT university</p>
                
                    <p className="ContentText">
                        Around 4+ years of experience in complete SDLC with delivering solutions in various tech and stack and proven ability in solving complex problems
                    </p>

                </div>
            </div>

            <div className="row mt-2">
                <div className="col">
                    <p className="Title text-center">INTERESTS</p>
                    <p className="ContentText">
                    Apart from being a software developer, I enjoy several hobbies. When it comes to outdoor, I play football over the weekends. 
                    When forced indoor, I love Gaming on my PS, Reading novels and learning is also one of the hobbies I enjoy, 
                    everyday I spend considerable amount of time on learning a new skillset it maybe tech, recreational or etc.
                    </p>
                </div>
            </div>

        </div>
    );
}
